<template>
<div  >
<!--  采集计算器样本-->
<!--  {{this.tmpCaption}}-->
<!--  {{this.pri_detail.treeNodeOrgId}}-->

  <el-button style="float:right;right:100px;" type="primary" @click="showCb">检测</el-button>


</div>
</template>

<script>

import Bus from "../../../utils/Bus.js"


export default {
  name: "DeviceInfoGateWayExample",


  mounted(){
    console.info("Mounted_DeviceInfoGateWayExample:"+this.pri_detail.treeNodeOrgId);

  },
  created(){
    console.info("Created_DeviceInfoGateWayExample:"+this.pri_detail.treeNodeOrgId);
    console.info("Created_DeviceInfoGateWayExample.treeNodeIds:"+this.pri_detail.treeNodeIds);
    console.info("Created_DeviceInfoGateWayExample.treeNodeType:"+this.pri_detail.treeNodeType);
    console.info("Created_DeviceInfoGateWayExample.currentTreeNode:"+JSON.stringify(this.pri_detail.currentTreeNode));
    console.info("Created_DeviceInfoGateWayExample.cbInfo:"+JSON.stringify(this.pri_detail.cbInfo));
  },
  props: {

    pri_detail: {
      type: Object,
      default: function () {
        return {
          treeNodeCaptions:[],
          treeNodeIds:[],
          treeNodeOrgId:'',
          currentTreeNode:{},
          treeNodeType:[],
          cbInfo: {},
        }

      }
    }
  },
  watch:{
    pri_detail:function(newVal,oldVal){
      console.info("watch_DeviceInfoGateWayExample点击树节点查询【采集计算器】新值:"+newVal.treeNodeOrgId+",旧值："+oldVal.treeNodeOrgId);
      console.info("watch_DeviceInfoGateWayExample点击树节点查询【采集计算器】新值:"+newVal.treeNodeIds+",旧值："+oldVal.treeNodeIds);
      console.info("watch_DeviceInfoGateWayExample点击树节点查询【采集计算器】当前树节点新值:"+JSON.stringify(this.pri_detail.currentTreeNode));
      console.info("watch_DeviceInfoGateWayExample点击树节点查询【采集计算器】type值:"+this.pri_detail.treeNodeType);
      console.info("watch_DeviceInfoGateWayExample点击树节点查询【采集计算器】type值:"+JSON.stringify(this.pri_detail.cbInfo));
      if(newVal.treeNodeOrgId!==oldVal.treeNodeOrgId){
       //查询

        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);

          /////////
          // this.$refs.query1.treeNodeInfo.treeNodeOrgId = newVal.treeNodeOrgId;
          // this.$refs.query1.treeNodeInfo.treeNodeCaptions = this.pri_detail.treeNodeCaptions;
          // this.$refs.query1.reQueryValveCurrentData() ;
          // ////////

        }
        console.info(this.tmpCaption);

      }else if(newVal.cbInfo!==oldVal.cbInfo){
        // console.info("原楼栋信息["+JSON.stringify(oldVal)+"],新楼栋信息["+JSON.stringify(newVal)+"]，请处理。");
        console.info("楼栋信息发生变化，请处理。");
      }
    }
  },

  data(){

    return {
      tmpCaption: [],

    }
  },
  methods:{
    showCb(){
      let data = {};
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      this.$emit("parent_method", "", val => {
          console.info("showCb_obj:"+ JSON.stringify(val) );
          data = val;
        }
      );

    },
    genLogicNo(){
      console.info("生成UUID:"+this.customGlobal.genLogicNo("CM"));
    },



  }
}
</script>

